.main {
    @include display-flex(column); 
    justify-content: flex-start;
    align-items: stretch;
    gap: 1.5rem;
    background: $background-color;
    padding-bottom: 2rem;
    @include block-shadow-line;
    
  }
  .main__header {
    @include  display-flex(row);
    justify-content: space-between;
    align-items: center;
    gap: 12px;
    margin-top: 24px;
  }
  .main__header::before{
    position: absolute;
    content: "";
    width: 4px;
    height: 40px;
    border-radius: 0 0.5rem 0.5rem 0;
    background: $header-mark-color;    
  }
  .header__text {
    font-size: 28px;
    line-height: 40px;
    letter-spacing: -0.6px;
    margin-left:1rem;
  }
  .header__menu-small{
    display: none;
  }
  .main__h-scroll-menu {
    @include  display-flex(row);    
    max-width: 1120px;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: center;
    gap: 0.5rem;
    overflow-y: auto;
    scrollbar-width: none; /*скрыть скролбар в Geko*/
    margin-left: 12px;
  }
  
  @supports selector(::-webkit-scrollbar) {
    .main__h-scroll-menu::-webkit-scrollbar {
      /*скрыть скролбар в WebKit*/
      overflow: hidden;
    }
  }
  .main__content {
    @include  display-flex(column);
    justify-content: flex-start;
    align-items: flex-start;
    gap: 1rem;
  }
  .content__article {
    @include  display-flex(column);    
    justify-content: flex-start;
    align-items: flex-start;
    gap: 1rem;
    margin-inline: 1rem;
    max-height: 90px;
    overflow: hidden;
    margin-inline: 1rem; 
  }
  .content__article-paragraph{
    font-weight: 100;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.2px;
    color: $content-text-color;
  }
  .content__text-block {
    @include  display-flex(column);
    justify-content: flex-start;
    align-items: flex-start;
  }
  .main__banner {
    width: 100%;
    height: 230px;
    object-fit: cover;
  }

  .content__button-arrow{
    margin-top: 1rem;
    margin-left: 2rem;
  }