@charset "UTF-8";
@font-face {
  font-family: "TT Lakes";
  src: url("../fonts/TTLakes-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 200;
}
@font-face {
  font-family: "TT Lakes";
  src: url("../fonts/TTLakes-Medium.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "TT Lakes";
  src: url("../fonts/TTLakes-DemiBold.woff") format("woff");
  font-style: normal;
  font-weight: 600;
}
@font-face {
  font-family: "TT Lakes";
  src: url("../fonts/TTLakes-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 800;
}
:root {
  font-family: "TT Lakes";
  font-size: 16px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background: #1b1c21;
}

.page {
  margin-inline: auto;
  min-width: 320px;
  max-width: 1120px;
  min-height: 1024px;
  padding-bottom: 1rem;
  background: #F8F8F8;
}

.button {
  min-width: fit-content;
  font-family: "TT Lakes";
  font-size: 1rem;
  line-height: 1.5rem;
  letter-spacing: -0.2px;
  background-position: center;
  background-repeat: no-repeat;
  background: transparent;
  border: none;
  cursor: pointer;
}

.button_type--border {
  height: 40px;
  padding: 0.5rem 1rem;
  background: #F8F8F8;
  color: #7E7E82;
}

.button_type--border--active {
  background: #FFFFFF;
  color: #7E7E82;
  padding: 0.5rem 1rem;
  border: 2px solid #B8FFEC;
  border-radius: 6px;
}

.button_type--circle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #FF3E79;
  transition: 0.25s;
}

.button_type--circle:hover {
  transform: scale(0.9) rotate(5deg);
  box-shadow: 0 0 10px 0 rgba(150, 150, 150, 0.5), 0 0 15px 0px rgba(20, 20, 20, 0.5);
}

.button-arrow {
  font-size: 1rem;
  line-height: 1.5rem;
  letter-spacing: -0.2px;
  color: #1B1C21;
}

.button-arrow::before {
  content: "";
  border-left: 2px solid #41F6D7;
  border-top: 2px solid #41F6D7;
  width: 5px;
  height: 5px;
  transition: rotate 1s;
  transform: rotate(-135deg);
  float: left;
  margin-top: 3px;
  margin-right: -16px;
}

.button-arrow::after {
  content: "";
  border-left: 2px solid #41F6D7;
  border-top: 2px solid #41F6D7;
  width: 5px;
  height: 5px;
  transition: rotate 1s;
  transform: rotate(-135deg);
  float: left;
  margin-top: 10px;
  margin-right: 16px;
}

.button-arrow--open::before {
  transition: 1s;
  transform: rotate(45deg) translate(0px, 0px);
}

.button-arrow--open::after {
  transition: 1s;
  transform: rotate(45deg) translate(0px, 0px);
}

/* ------------- SET ICON ---------------------- */
.up-menu__button-burger {
  background-image: url("../img/ico/burger.svg");
}

.up-menu__button-call,
.mobile-menu__button-call {
  background-image: url("../img/ico/call.svg");
}

.up-menu__button-chat,
.mobile-menu__button-chat {
  background-image: url("../img/ico/chat.svg");
}

.up-menu__button-profile,
.mobile-menu__button-profile {
  background-image: url("../img/ico/profile.svg");
}

.up-menu__button-repair {
  background-image: url("../img/ico/repair.svg");
}

.up-menu__button-checkstatus {
  background-image: url("../img/ico/checkstatus.svg");
}

.menu-small__button-repair {
  background-image: url("../img/ico/repair.svg");
}

.menu-small__button-checkstatus {
  background-image: url("../img/ico/checkstatus.svg");
}

.mobile-menu__button-close {
  background-image: url("../img/ico/close.svg");
}

.mobile-menu__button-search {
  background-image: url("../img/ico/search.svg");
}

.up-menu {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  min-height: 88px;
  background: #fff;
  border-bottom: 2px solid #D9FFF5;
}

.logo {
  margin: 0;
  padding: 0;
  width: 96.55px;
  height: 50.81px;
}

.logo__image {
  object-fit: contain;
}

.up-menu__left {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
  flex-basis: 60px;
  margin-left: 1rem;
}

.up-menu__left::after {
  position: relative;
  content: "";
  width: 0px;
  height: 2rem;
  background-color: #EAEAEA;
  border: 1px solid #EAEAEA;
}

.up-menu__center {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-inline: 1rem;
  flex-basis: 100%;
}

.up-menu__social-menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.up-menu__right {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  gap: 1rem;
  flex-basis: 100px;
  margin-right: 1rem;
}

.up-menu__right::before {
  position: relative;
  content: "";
  width: 0px;
  height: 2rem;
  background-color: #EAEAEA;
  border: 1px solid #EAEAEA;
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  gap: 1.5rem;
  background: #F8F8F8;
  padding-bottom: 2rem;
  border-bottom: 2px solid #D9FFF5;
}

.main__header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  margin-top: 24px;
}

.main__header::before {
  position: absolute;
  content: "";
  width: 4px;
  height: 40px;
  border-radius: 0 0.5rem 0.5rem 0;
  background: #41F6D7;
}

.header__text {
  font-size: 28px;
  line-height: 40px;
  letter-spacing: -0.6px;
  margin-left: 1rem;
}

.header__menu-small {
  display: none;
}

.main__h-scroll-menu {
  display: flex;
  flex-direction: row;
  max-width: 1120px;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  gap: 0.5rem;
  overflow-y: auto;
  scrollbar-width: none; /*скрыть скролбар в Geko*/
  margin-left: 12px;
}

@supports selector(::-webkit-scrollbar) {
  .main__h-scroll-menu::-webkit-scrollbar {
    /*скрыть скролбар в WebKit*/
    overflow: hidden;
  }
}
.main__content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 1rem;
}

.content__article {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 1rem;
  margin-inline: 1rem;
  max-height: 90px;
  overflow: hidden;
  margin-inline: 1rem;
}

.content__article-paragraph {
  font-weight: 100;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  color: #1B1C21;
}

.content__text-block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.main__banner {
  width: 100%;
  height: 230px;
  object-fit: cover;
}

.content__button-arrow {
  margin-top: 1rem;
  margin-left: 2rem;
}

.service-swiper, .technique-swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 1;
}

.swiper-wrapper {
  margin-left: var(--pad-left);
}

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background-color: #DDDDDD;
}

.swiper-pagination-bullet-active {
  width: 12px;
  height: 12px;
  background-color: #B5B6BC;
}

.elements__item {
  display: flex;
  flex: 0 0 30%;
}

.element {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-inline: 1rem;
  border: 1px solid #EAEAEA;
  background: #FFFFFF;
  height: 72px;
  width: 100%;
  border-radius: 5px;
  gap: 1rem;
}

.element__image {
  height: 40px;
  width: 90px;
  object-fit: contain;
}

.element__button {
  width: 40px;
  height: 40px;
  border: 2px solid #FF3E79;
  border-radius: 50%;
  background: #FFFFFF;
}

.element__button::after {
  position: relative;
  display: block;
  content: "";
  width: 0.4rem;
  height: 0.4rem;
  border-top: 2px solid #FF3E79;
  border-right: 2px solid #FF3E79;
  transform: translateX(0.9rem) rotate(45deg);
}

.panel {
  background-color: #F8F8F8;
  --pad-left: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 1rem;
  max-width: 1120px;
  padding-bottom: 1rem;
  border-bottom: 2px solid #D9FFF5;
}

.panel__header {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  background: linear-gradient(to right, #F8F8F8, #FFFFFF);
}

.panel__header-text {
  text-transform: uppercase;
  font-family: "TT Lakes";
  font-size: 1rem;
  font-weight: 500;
  margin: 1rem;
  margin-left: var(--pad-left);
  line-height: 1.5rem;
  letter-spacing: -0.2px;
  color: #7E7E82;
}

.panel__elements {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 1rem;
}

.panel__paginator--hide {
  display: none;
}

.panel__button-arrow {
  margin-left: 2rem;
}

.panel__elements--large {
  /* large screen */
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  align-items: flex-start;
  gap: 1rem;
  transition-duration: 1s;
}

.panel__elements-large--close {
  height: calc(144px + 1.2rem);
}

.swiper-wrapper {
  margin-left: var(--pad-left);
}

.wrapper-large {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  gap: 1rem 2em;
  overflow: hidden;
  padding-inline: var(--pad-left);
}

.technique-panel .panel__elements {
  height: 205px;
}
.technique-panel .element {
  height: 160px;
  min-width: 224px;
  max-width: 240px;
  border-radius: 5px;
  gap: 1rem;
  padding: 1rem 0.5rem;
}
.technique-panel .element .element__header {
  align-self: flex-start;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21;
}
.technique-panel .element .element__button {
  align-self: flex-end;
}
.technique-panel .elements__item {
  display: flex;
  flex-direction: column;
  flex: 0 0 20%;
}

.price-panel .wrapper {
  display: flex;
  flex-direction: column;
  justify-content: stretch;
  align-items: center;
  padding-inline: 1.5rem;
  margin-inline: 2rem;
}
.price-panel .wrapper .back {
  position: absolute;
  transform: translateY(40px);
  width: 92%;
  max-width: 1050px;
  height: 310px;
  background-color: white;
  border-radius: 0.5rem;
  z-index: 1;
}
.price-panel .wrapper .elements__item {
  width: 100%;
}
.price-panel .wrapper .elements__item .element {
  z-index: 2;
  width: 100%;
  padding-inline: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 62px;
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #EAEAEA;
}
.price-panel .wrapper .elements__item .element .element__content {
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.price-panel .wrapper .elements__item .element .element__content .element__header {
  display: none;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  font-weight: 200;
  color: #7E7E82;
}
.price-panel .wrapper .elements__item .element .element__content .element__description {
  display: flex;
  font-size: 14px;
  font-weight: 200;
  line-height: 18px;
  letter-spacing: 0.2px;
  color: #1B1C21;
}
.price-panel .wrapper .elements__item .element .element__content .description-text {
  flex: 0 0 350px;
}
.price-panel .wrapper .elements__item .element .element__content .description-price {
  flex: 0 0 76px;
}
.price-panel .wrapper .elements__item .element .element__content .description-time {
  flex: 0 0 82px;
}
.price-panel .wrapper .elements__item .element .element__button {
  font-size: 12px;
  font-weight: 800;
  line-height: 1.5rem;
  letter-spacing: -0.15px;
  text-align: left;
  font-variant: small-caps;
  width: 119px;
  height: 32px;
  padding-left: 0.75rem;
  padding-block: 0.25rem;
  border-radius: 1rem;
  border: none;
  background-color: #FF3E79;
  color: #FFFFFF;
  cursor: pointer;
}
.price-panel .wrapper .elements__item .element .element__button::before {
  display: none;
}
.price-panel .wrapper .elements__item .element .element__button::after {
  content: "";
  display: block;
  border: none;
  border-left: 2px solid white;
  border-top: 2px solid white;
  width: 7px;
  height: 7px;
  transform: translate(80px, -16px) rotate(135deg);
}
.price-panel .panel__offer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
  margin: 1rem;
}
.price-panel .panel__offer .panel__offer-text {
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
  font-weight: 200;
  color: #1B1C21;
}
.price-panel .panel__offer .panel__offert-button {
  font-size: 1rem;
  line-height: 1.5rem;
  letter-spacing: -0.2px;
  font-weight: 600;
  color: #1B1C21;
  text-align: start;
}
.price-panel .panel__offer .panel__offert-button::after {
  position: absolute;
  content: "";
  width: 7px;
  height: 7px;
  border-top: 2px solid #FF3E79;
  border-left: 2px solid #FF3E79;
  transform: translate(0.5rem, 0.5rem) rotate(135deg);
}

.element-header .element {
  height: 40px !important;
  border: none !important;
  background-color: #F8F8F8;
}
.element-header .element__description {
  color: #7E7E82 !important;
}
.element-header .element__button {
  visibility: hidden;
}

@media screen and (max-width: 768px) {
  .price-panel .wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-inline: 1rem;
    margin-inline: 1rem;
    gap: 1rem;
  }
  .price-panel .wrapper .back {
    display: none;
  }
  .price-panel .wrapper .elements__item .element {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 260px;
    min-height: 180px;
    padding-inline: 1rem;
    border-radius: 0.5rem;
    border: none;
  }
  .price-panel .wrapper .elements__item .element .element__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    padding-bottom: 1rem;
  }
  .price-panel .wrapper .elements__item .element .element__content .element__header {
    display: block;
    margin-top: 1rem;
  }
  .price-panel .wrapper .elements__item .element .element__content .description-text {
    flex: 0 0 100%;
  }
  .price-panel .wrapper .elements__item .element .element__content .description-price {
    flex: 0 0 100%;
  }
  .price-panel .wrapper .elements__item .element .element__content .description-time {
    flex: 0 0 100%;
  }
  .price-panel .wrapper .elements__item .element .element__button {
    position: absolute;
    transform: translate(50px, 50px);
  }
  .element-header {
    display: none;
  }
}
.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  margin: 1rem;
  gap: 1rem;
}
.footer .footer-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-basis: 0 0 30%;
  font-size: 14px;
  font-weight: 200;
  line-height: 1rem;
  color: #7E7E82;
}
.footer .footer-content .footer__creator {
  min-width: 218px;
}
.footer .footer-content .footer__politic {
  min-width: 214px;
}
.footer .footer-content .footer__info {
  max-width: 330px;
}

@media screen and (max-width: 768px) {
  .footer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
  }
  .footer .footer-content {
    justify-content: center;
    width: 100%;
  }
  .footer .footer-text {
    text-align: center;
  }
}
@media screen and (max-width: 768px) {
  .services-panel {
    height: 230px;
  }
  .services-panel .panel__elements {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 116px;
    gap: 1rem;
  }
  .services-panel .elements__item {
    flex: 0 0 240px;
  }
}
@media screen and (min-width: 900px) {
  .services-panel {
    --pad-left: 2rem;
  }
  .services-panel .elements__item {
    flex: 0 0 22%;
  }
}
@media screen and (max-width: 350px) {
  .up-menu__left::after {
    display: none;
  }
}
@media screen and (max-width: 500px) {
  .up-menu__social-menu {
    display: none;
  }
}
@media screen and (min-width: 600px) {
  .main {
    gap: 2rem;
  }
  .main__content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 48px;
  }
  .content__text-block {
    flex-basis: 50%;
  }
  .main__banner {
    flex-basis: 50%;
    min-height: 305px;
    border-radius: 0.5rem;
  }
  .content__article {
    max-height: 160px;
  }
}
@media screen and (min-width: 1120px) {
  .page__up-menu,
.fade-place__fade {
    display: none;
  }
  .main {
    gap: 2rem;
  }
  .header__menu-small {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    gap: 2rem;
    padding-right: 2rem;
  }
  .menu-small__button-define {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    gap: 0.5rem;
  }
  .main__h-scroll-menu {
    flex-wrap: wrap;
  }
}